.confirm {
  position: fixed;
  top: -20%;
  left: 50%;
  width: 15rem;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 6px gray;
  padding: .5rem 1.5rem;
  border-radius: .25rem;
  background-color: white;
  text-align: center;
  z-index: 1000;
  transition: .4s ease-in-out;

  &>p {
    font-weight: bold;
  }

  .buttons {
    justify-content: space-between;

    div {
      font-size: .9rem;
      display: flex;
      flex-direction: row;
    }
  }

  &.show {
    top: 50%;
  }

  p {
    padding: .5rem;
  }
}